<template>
  <div class="login">
    <div class="login-box">
      <span>{{loginName}}</span>
      <div class="flex-row">
        <el-input v-model="user" class="user-name" placeholder="请输入用户名"></el-input>
      </div>
      <div class="flex-row">
        <el-input placeholder="请输入密码" v-model="password" class="user-name" show-password></el-input>
      </div>
      <el-button type="success" @click="login">登录</el-button>
      <div class="flex-row">
        <div class="item" @click="loginChoose(1)">前台登录</div>
        <div class="item" @click="loginChoose(2)">后台登录</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
/* eslint-disable */
export default {
  name: 'login',
  data () {
    return {
      user: '',
      password: '',
      loginName: '后台登录'
    }
  },
  computed: {
    doorKey () {
      return this.$store.state.doorKey
    }
  },
  methods: {
    ...mapActions(['saveDoorKey']),
    login () {
      if (this.loginName === '后台登录') {
        console.log(111)
        if (this.user === 'admin' && this.password === '123456') {
          this.$router.push({ name: 'end' })
          this.doorKey = true
          this.saveDoorKye(this.doorKey)
        } else if (!this.user || !this.password) {
          this.$message.error('数据不能为空')
          return 0
        } else if (this.user !== 'admin') {
          this.$message.error('该用户不存在')
          return 0
        } else if (this.password !== '123456') {
          this.$message.error('密码填写错误')
          return 0
        }
      } else {
        if (this.user === 'nanjing' && this.password === '123456') {
          this.$router.push({ name: 'front' })
        }
      }
    },
    loginChoose (n) {
      this.loginName = n === 1 ? '前台登录' : '后台登录'
    }
  }
}
</script>

<style lang='scss'>
.login {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-box {
  height: 400px;
  width: 400px;
  padding: 50px;
  border: 2px solid orange;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  .flex-row {
    display: flex;
    justify-content: space-around;
    .item {
      cursor: pointer;
    }
    .item:hover {
      color: #6c6;
    }
    .user-password,
    .user-name {
      flex: 1;
      height: 30px;
      border: none;
    }
  }
}
</style>
